<template>
  <div>
    <p>网格 + JavaScript 操作的行跨度</p>
    <p>安迪·赤足写了一本很棒的指南。</p>
    <p>诀窍是设置相当短的重复网格行，让元素尽可能水平地落入网格，然后调整它们的高度以与网格匹配，</p>
    <p>并使用一些相当简单的数学来计算它们应该跨越多少行。</p>
    <div class="grid">
      <v-card
        v-for="(item, index) in 30"
        :id="item"
        :key="'card' + item"
        color="primary"
        class="item"
        :class="{
          'blog': index % 4 === 0,
          'photo': index % 4 === 1,
          'project': index % 4 === 2,
          'post': index % 4 === 3,
        }"
      >
        <div class="title">
          <h3>
            {{ index + 1 }}:
            {{ index % 4 === 0 ? 'Blog' : '' }}
            {{ index % 4 === 1 ? 'Photo' : '' }}
            {{ index % 4 === 2 ? 'Project' : '' }}
            {{ index % 4 === 3 ? 'Post' : '' }}
          </h3>
        </div>
        <div class="desc content">
          <span>Lorem ipsum dolor sit amet, consectetur adipiscing elit.</span>
          <span
            v-for="text in Math.floor(Math.random() * index)"
            :key="'text' + text"
          >
            {{ text }}Lorem ipsum dolor sit amet, consectetur adipiscing elit.
          </span>
        </div>
      </v-card>
    </div>
  </div>
</template>

<script>
  export default {
    mounted() {
      function resizeGridItem(item){
        const grid = document.getElementsByClassName("grid")[0];
        const rowHeight = parseInt(window.getComputedStyle(grid).getPropertyValue('grid-auto-rows'));
        const rowGap = parseInt(window.getComputedStyle(grid).getPropertyValue('grid-row-gap'));
        const rowSpan = Math.ceil((item.querySelector('.content')?.getBoundingClientRect().height+rowGap+60)/(rowHeight+rowGap));
          item.style.gridRowEnd = "span "+rowSpan;
      }
      const allItems = document.getElementsByClassName("item");
      for(let x=0;x<allItems.length;x++){
        resizeGridItem(allItems[x]);
      }
    },
  }
</script>

<style lang="scss" scoped>
.grid {
  display: grid;
  grid-gap: 10px;
  grid-template-columns: repeat(auto-fill, minmax(250px,1fr));
  grid-auto-rows: 20px;
}

.item {
  background-color: #ffffff;
}

.photothumb{
  width: 100%;
}

.title {
  padding: 20px;
}

.title h3{
  font-size: 1.2em;
  color: #ffffff;
  text-transform: uppercase;
}

.project .title{
  background-color: #607D8B;
}

.blog .title{
  background-color: #ff9800;
}

.photo .title{
  background-color: #f44336;
}

.desc{
  padding: 10px 10px 5px 10px;
}

.desc img{
  width: 50%;
  margin: 0 10px 10px 0;
  float: left;
}

.desc p{
  margin-bottom: 10px;
}

</style>